import React from 'react';
import {Select, Box} from 'theme-ui';

import typeStyle from './style/type.module.scss';
import Timer from './timer';

class Topbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            wpm: '~~',
            modes: [{
                key: "random",
                value: "random",
                text: "random"
            }, {
                key: "paragraphs",
                value: "paragraphs",
                text: "paragraphs"
            }]
        }
    }

    render() {
        return (
            <div className={typeStyle.top}>
                <Select className={typeStyle.select} defaultValue={'Random'}>
                    <option>Random</option>
                    <option>Paragraph</option>
                </Select>
                <Select className={typeStyle.select} defaultValue={'Random'}>
                    <option>Random</option>
                    <option>Paragraph</option>
                </Select>
                <Box style={{"float": "right"}} bg={'primary'}>{this.state.wpm}wpm</Box>
                <Timer timer={this.props.timer}/>
            </div>
        )
    }
}

export default Topbar;