import React from 'react';

import Textfield from './textfield';
import Timer from './timer';
import wordsList from '../images/words';
import Bottombar from './bottombar';
import Topbar from './topbar';

class Type extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: false,
            timer: false,
            word: '',
            words: '',
            time: null
        }
    }

    componentDidMount() {
        this.setState({words: this.read(30)});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (!this.state.timer && this.state.word !== '' && !this.state.disabled) {
            this.setState({
                timer: true,
                time: new Date().getTime()
            });
        }
    }

    manageTextfield = childData => {
        if (childData) {
            let date = new Date();
            const time = date.getTime() - this.state.time;
            console.log(this.state.words.length * 12000 / time);
            this.setState({
                disabled: childData,
                timer: false,
                time
            });
        } else {
            this.setState({
                word: ''
            });
        }
    };

    inputCallback = childData => {
        if (childData.reset) {
            this.setState({
                words: this.read(30),
                disabled: false,
                timer: false
            })
        }
        this.setState({
            word: childData.word
        });
    };

    read(num) {
        let wordsArray = [];
        wordsList.words.forEach(word => wordsArray.push(word.englishWord));
        let result = new Array(num);
        for (let i = 0; i < num; i++) {
            const x = Math.floor(Math.random() * 1000);
            result[i] = wordsArray[x];
        }
        return result.join(' ').trim();
    };

    render() {
        return (
            <div>
                <Topbar timer={this.state.timer}/>
                <Textfield
                    word={this.state.word}
                    words = {this.state.words}
                    parentCallback = {this.manageTextfield}
                />
                <Bottombar
                    word={this.state.word}
                    disabled={this.state.disabled}
                    parentCallback = {this.inputCallback}
                />
            </div>
        )
    }
}

export default Type;